
.project-container{
    
    font-family: 'Poppins', sans-serif;
}

.project-header{
    padding-top: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    font-weight: 600;
    color: rgb(255,180,0);
}
.why{
    color: #fff;
}
.project-header h3{
    font-size: 40px;
    font-weight: 800;
}
.project-header p{
    font-size: 15px;
    font-weight: 500;
    letter-spacing: 3px;
}

.header-underline{
    height: 3px;
    width: 200px;
    background-color:rgb(255,180,0);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header-underline1{
    height: 9px;
    width: 40px;
    background-color: rgb(255,180,0);
    position: absolute;
    border-radius: 5px;
}

.projectDetails{
    background-image: grey;
    margin-top: 50px;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 2;
}
.projectCard{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(350px,1fr));
    grid-template-rows: repeat(200px 200px);
    height: fit-content;
    width: 80%;
    justify-content: center;
    font-family: 'roboto', sans-serif;
    color: white;
    margin: auto;
    gap: 30px;
   
}

.project-card{
     border-radius: 10px;
    overflow: hidden;
    background-color: rgb(27,27,27);
    box-sizing: border-box;
    border: solid rgb(255,180,0) 2px;
   
} 
.project-description{
    height: 70px;
}

.project-card:hover{
    transform: scale(1.001);
    margin-top: -10px;
    transition: .15s ease-in;
    cursor: pointer;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    
}

.projectImg{
    border-top: 1px solid rgb(242, 242, 242);
    border-bottom: 1px solid rgb(242, 242, 242);
   
}
.cartImg{
    width: 100%;
}


.projectInfo{
    padding: 20px 10px;
}
.project-title{
    text-align: center;
    padding: 10px 0;
    
}
.projectLinks{
    margin-bottom: 20px;
    margin-top: 20px;
}
.projectLinks .project-github-link{
    border: solid #fff 1px;
    border-radius: 6px;
    color: #fff;
    color: rgb(255,102,0);
    padding: 8px 50px;
    margin-left: 10px;
    cursor: pointer;
    text-decoration: none;
    
}


.projectLinks .project-deployed-link{
    border: solid #fff 1px;
    border-radius: 6px;
    color: rgb(65, 109, 127);
    color: #fff;
    color: rgb(255,102,0);
    padding: 8px 50px;
    margin-left: 10px;
    cursor: pointer;
    text-decoration: none;
}
.projectLinks .project-deployed-link:hover{
    background-color: rgb(255,180,0);
    color: #fff;
}
.projectLinks .project-github-link:hover{
 
    border: solid rgb(255,180,0) 0px;
    background-color: rgb(255,180,0);
    color: #fff;
}
.project-description{
    text-align: justify;
    padding: 5px;
}
.project-tech-stack{
     padding: 15px; 
    
}
.project-tech-stack .stackk{
    display: flex;
   gap: 10px;
}


.project-description{
    height: auto;
}

.span{
    padding: 8px;
    border-radius: 5px;
    border: solid #fff 1px;
    color: rgb(255,102,0);
}


@media screen and (max-width: 480px){
  
    .project-card{
        margin: 15px 00px;
    }
    
    .projectLinks .project-github-link, .projectLinks .project-deployed-link{
        padding: 8px 15px;
    }

.project-tech-stack{ 
margin-top: 10px;
width: auto;
}

.project-description{
    height: auto;
}

.span{
    border: solid #fff 1px;
    padding: 8px;
    border-radius: 5px;
    font-size: 10px;
    color: rgb(255,102,0);
    text-align: center;
}

.project-tech-stack .stackk{
  display: grid;
  grid-template-columns: repeat(3,70px);
  grid-template-rows: auto;
  justify-content: center;

}

.projectLinks{
    margin: auto;
    width: 90%;
    display: flex;
    justify-content: center;
}
.projectLinks .project-github-link{
    margin-left: -2px;
}
}












