
.NavMenuBar{
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    top: 0;
    position: fixed;
    width: 100%;
    z-index: 1000;
}
.NavMenuBar .navMenuLogo{
    font-size: 25px;
    color: rgb(255,180,0);
    margin-top: 8px;
}
.navMenuLogo > a{
   
    cursor: pointer;
}
.NavMenuBar .toggleMenuButton{
    font-size: 25px;
}

.navMenuContainer{
    padding: 5px 20px;
    display: flex;
    justify-content: space-between;
    color: #fff;
}
.stickyy{
    background: rgb(17,17,17);
}

#navMenuCollapsibleMenu{
    padding: 0 10px;
    border-top: 1px solid #fff;
    background: rgb(17,17,17);
}
.InActiveNavMenu{
    display: none;
}
.activeNavMenu{
    display: block;
}
.navMenuCollapsibleArea{
    display: flex;
    justify-content: space-between;
}
.navMenuCollapsibleArea a{
    font-size: 13px;
    padding: 10px;
    color: rgb(246, 243, 243);
    cursor: pointer;
}
.navMenuBarMain{
    display: flex;
    align-items: center;
    
}
.navMenuBarMain a{
    margin: 0px 20px;
    cursor: pointer;
}
.toggleMenuButton{
    display: none;
}


@media screen and (max-width: 480px){
    .navMenuBarMain{
        display: none;
        border: solid red 1px;
      
    }
    .toggleMenuButton{
        display: block;
    }
    .stickyy{
        background: rgb(17,17,17);
    }
    .navMenuCollapsibleArea{
       padding: 2px;
    }
    .navMenuCollapsibleArea a{
        font-size: 12px;
        padding: 6px;
        
    }

    .bbk{
        border: rgb(255,180,0) 2px solid;
        font-family: 'Montserrat', sans-serif;
        background-color: transparent;
        color: #fff;
        cursor: pointer;
        border-radius: 8px;
        
    }
}  



.hColor{
    color:white;
    text-decoration:none
}

.hoverColor:hover{
    color:  rgb(255,180,0);
}
.hColor:hover{
    color:  rgb(255,180,0);
}

.bbkvines{
    border: rgb(255,180,0) 2px solid;
    padding: 8px 15px 8px 15px ;
    font-size: 15px;
    letter-spacing: 1.5px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    background-color: transparent;
    color: #fff;
    cursor: pointer;
    border-radius: 8px;
    margin-top: 10px;
}
.bbkvines:hover{
    background-color: rgb(255,180,0);
}



